import { useEffect } from "react"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { getHasDocAndIsApple, getIsAppleChrome, getIsApplePWA } from "./documentViewerPage.utils"

type UseDocumentViewerPageScrollingProps = {
  documentId: string
}

const useDocumentViewerPageScrolling = ({
  documentId,
}: UseDocumentViewerPageScrollingProps): void => {
  const { isMacOS, isIOS, isSafari, isPWA } = useDeviceInfo()

  useEffect(() => {
    const isAppleChrome = getIsAppleChrome({ isMacOS, isIOS, isSafari })
    const isApplePWA = getIsApplePWA({ isMacOS, isIOS, isPWA })
    const hasDocAndIsApple = getHasDocAndIsApple({ documentId, isAppleChrome, isApplePWA })

    if (hasDocAndIsApple) {
      window.document.documentElement.classList.add("disabled-overscroll")
    } else {
      window.document.documentElement.classList.remove("disabled-overscroll")
    }

    return () => {
      window.document.documentElement.classList.remove("disabled-overscroll")
    }
  }, [documentId, isIOS, isMacOS, isPWA, isSafari])
}

export { useDocumentViewerPageScrolling }
